import React, { Component } from "react";
import { connect } from "react-redux";

import "./App.css";
import Todolist from "../components/Todolist";
import InputInButtons from "../components/inpuInButtons";
import { getFormValues } from "redux-form";
import AfterAdd from "../components/afterAdd";

class App extends Component {

  handleAdd = () => {
    const { add, values } = this.props;

    add(values);
    return (values.clubName = undefined);
  };

  handleDelete = (v, e) => {
    const { del } = this.props;
    e.preventDefault();
    del(v);
  };
  handleEdit = (v, e) => {
    e.preventDefault();
    const { edit } = this.props;
    edit(v);
  };
  handleClose = (v, e) => {
    e.preventDefault();
    const { close } = this.props;
    close(v);
  };
  newValueForAction = v => {
    const { addNewValue, newValue } = this.props;

    addNewValue(newValue, v);
    newValue.InputInButtons = "";
  };
  handleCross=(v,e)=>{
    console.log(e);
      e.preventDefault();
      const { cross } = this.props;
      cross(v);
  };
  handleCompleted (){

      const {completed} = this.props;
      completed()
  }
    handleShowAll (){

        const {showAll} = this.props;
        showAll()
    }
    handleActive (){

        const {active} = this.props;
        active()
    }

  render() {
    const { store, close, values,v } = this.props;

    return (
      <div className="App container col-6 pr-0">
        <Todolist
          onSubmit={() => this.handleAdd()}
          input={values}

        />
        <ul className="">
          {store.todoListReducer.map(v => (
              (v.show?
            <li
              key={v.id}
              className="row container-fluid input-group justify-content-between pr-0 m-0 mt-1"
            >
              <div className={`row col-auto ${v.crossed?'App-cross-text':null}`}
              onClick={(e)=>this.handleCross(v,e)}
              >{v.values}</div>

              {v.clicked ? (
                <InputInButtons
                  v={v}
                  handleClose={(v, e) => this.handleClose(v, e)}
                  close={close}
                  onSubmit={() => this.newValueForAction(v)}
                />
              ) : null}

              {v.clicked ? null : (
                <AfterAdd
                  v={v}
                  handleEdit={(v, e) => this.handleEdit(v, e)}
                  handleDelete={(v, e) => this.handleDelete(v, e)}
                />
              )}

            </li>:null)

          ))}
        </ul>
          {store.todoListReducer.length?<div>
  <button className="btn btn-secondary" onClick={()=>this.handleActive()}>Active</button>
  <button className="btn btn-secondary" onClick={()=>this.handleCompleted()}>Completed</button>
  <button className="btn btn-secondary" onClick={()=>this.handleShowAll()}>Show all</button>
          </div>:null}
      </div>
    );
  }
}
const mapStateToProps = state => {
  return {
    store: state,
    values: getFormValues("Todolist")(state),
    newValue: getFormValues("InputInButtons")(state)
  };
};
const mapDispatchToProps = dispatch => {
  return {
    add: values => dispatch({ type: "ADD", values }),
    edit: v => dispatch({ type: "EDIT", id: v.id }),
    del: v => dispatch({ type: "DELETE", id: v.id }),
    close: v => dispatch({ type: "CLOSE", id: v.id }),
    addNewValue: (newValue, v) => {
      dispatch({ type: "NEWVALUE", value: newValue, id: v.id })
    },
      cross: v => dispatch({type:"CROSS", id:v.id,crossed:false}),
      completed: () => dispatch({ type: "COMPLETED" }),
      showAll: () => dispatch({ type: "SHOWALL" }),
      active: () => dispatch({ type: "ACTIVE" })
  };


};

export default connect(
  mapStateToProps,
  mapDispatchToProps
)(App);
